.table{
    overflow-y: scroll;
    height:350px;
    color:black;
    overflow-x: hidden;
    margin-bottom: 5%;
}

.table tr{
    display: flex;
    justify-content: space-between;
}

.table td{
    padding: 0px 5%;
}

.table tr:nth-of-type(odd){
    background-color: #eee;
}

.table strong{
    font-size: smaller;
}


.table::-webkit-scrollbar-track-piece{
    background: #ddd;
}