/* @import url('https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap'); */

@font-face {
  font-family: BalloTammudu2;
  src: url(fonts/BalooTammudu2-Medium.ttf);
}

*{
    margin:0 ;
    padding: 0 ;
    box-sizing: border-box;
    font-family: BalloTammudu2;
    /* font-family:   monospace; */
}

.app{
    display: flex;
    flex-direction: column;
}


.app__header{
    padding: 10px;
    color: white;
    background: black;
    display: flex;
    justify-content: space-evenly;
}

.title{
    font-family: monospace;
    font-weight: 900;
    font-size: 2em;
}

.app__header>.app__dropdown{
    background-color: greenyellow;
}

.MuiSelect-outlined{
    font-weight: 600 !important;
}

.app{
    background:linear-gradient(#000000,#434343);

}

.content{
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    color: white;
}


.app__left{

flex-shrink: 1;
}

.app__mid{
flex-basis: 60%;
flex-shrink: 1;
    display: flex-column;
    height: 100%;
}

.app__right{
flex-basis: 20%;
flex-shrink: 1;

}

    /* .app__right{
        display: flex;
    } */

::-webkit-scrollbar{
    width: 10px;
    border-radius: 12px;
}

::-webkit-scrollbar-thumb{
    border-radius: 12px;
	background-color: #555;
}

::-webkit-scrollbar-track-piece{
    background:black;
}

@media (max-width:990px){
    .content{
        flex-direction: column;
    }
}

h2.news{
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}