
.MuiTypography-root{
  margin: 0 !important;
}

.infoBox {
  background-color: pink !important;
  width: 85%;
  height: fit-content;
  margin: 10px;
}

.infoBox:not(:last-child) {
  margin-right: 10px;
}

.infoBox--selected {
  border-top: 10px solid greenyellow;
}

.infoBox--red {
  border-color: red;
}


.infoBox__cases {
  color: red;
  font-weight: 600;
  font-size: 1.75rem;
  
}

.infoBox__cases--green {
  color: lightgreen !important;
}

.infoBox__total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}

Muicardcontent{
  color: red !important;
}

@media (max-width:990px){
  .infoBox{
    width:100vw;
  }
}